
#app {
  min-width: 320px;
  max-width: 780px; 
  margin: 15px;
  background-color: #0b0c10;
  border-radius: 15px;
  padding: 5px;
}
.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#display {
  background-color: #1f2833;
  border-radius: 10px;
}
.btn {
  text-align: center;
  width: 16%;
  height: 75px;
  margin: 10px;
  font-size: 2em;
  border-radius: 50%;
  background-color: #45a29e;
  border-color: #66fcf1;
  color: white;
}
.row {
  width: 100%;
}
h1 {
  font-weight: bold;
  text-align: center;
  font-size: 3em;
  color: #66fcf1;
}



